import React, { Component } from "react";
import { connect } from "react-redux";
import { reduxForm } from "redux-form";
// import { ReanderSelect } from "../../../components/helpers/field";

const maptostate = (state) => {
  return {
    setCamera: state.databarang.setCamera,
    hideModal: state.datamaster.modalDialog,
  };
};

class ModalPesanan extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {
    setTimeout(() => {
      document.getElementById("nomor_pesanan").focus();
    }, 100);
  }
  render() {
    return (
      <>
        {/* <form onSubmit={this.props.handleSubmit} autoComplete="off"> */}
          <label htmlFor="">Masukan Nomor Pesanan</label>
          <div className="input-group mb-3">
            <input
              type="text"
            id="nomor_pesanan"
              className="form-control"
              placeholder="Nomor Pesanan"
              aria-label="Nomor Pesanan"
              aria-describedby="basic-addon2"
            />
            <div className="input-group-append">
              <button
                className="btn btn-primary"
                type="button"
                onClick={() =>
                  this.props.getNomorPesanan(
                    document.getElementById("nomor_pesanan")
                  )
                }
              >
                {this.props.isLoadingPesanan ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Search
                  </>
                ) : (
                  "Search"
                )}
              </button>
            </div>
          </div>
       
      </>
    );
  }
}

ModalPesanan = reduxForm({
  form: "ModalPesanan",
  enableReinitialize: true,
})(ModalPesanan);
export default connect(maptostate, null)(ModalPesanan);
